<template>
    <div class="body-control-form">
        <b-container class="control-form-container text-center">
            <b-form @submit="newMessage">
                <b-row>
                    <b-col md="12">
                        <b-form-group class="form-group" id="email" label="Email: *" label-for="input-email">
                            <b-form-input
                            id="input-email"
                            v-model="form.email"
                            type="email"
                            required
                            placeholder="Enter email"
                            ></b-form-input>
                        </b-form-group>

                        <b-form-group class="form-group" id="input-group-2" label="Your Name:" label-for="input-2">
                            <b-form-input
                            id="input-2"
                            v-model="form.name"
                            placeholder="Enter name"
                            ></b-form-input>
                        </b-form-group>

                        <label>Message: *</label>
                        <b-form-textarea class="mt-2" id="textarea" v-model="form.message" placeholder="What's on your mind ?" 
                        rows="3" max-rows="6" required></b-form-textarea>

                        <b-button type="submit" class="btn-outline mt-3">Send</b-button>
                    </b-col>
            </b-row>
            <b-row>
                <b-col md="12" class="informations-card text-center p-5 mt-3">
                    <h1 hidden>
                        Contacts
                    </h1>
                    <p>
                        NOTICE : Contact form above is not currently working, please contact me here instead
                    </p>
                    <a href="https://www.facebook.com/RJM.Sniper.X.05/"><font-awesome-icon class="mr-3 contact-icons" :icon="{ prefix: 'fab', iconName: 'facebook-f' }" /></a>
                    <a href="https://www.linkedin.com/in/ronald-madrid-2254941ba/"><font-awesome-icon class="mr-3 contact-icons" :icon="{ prefix: 'fab', iconName: 'linkedin-in' }" /></a>
                </b-col>
            </b-row>
            </b-form>    
        </b-container>    
    </div>    
</template>

<script>
export default {
    name: 'ContactForm',
    data() {
      return {
        form: {
          email: '',
          name: '',
          message: ''
        }
      }
    },
    methods: {
      newMessage(e) {
        e.preventDefault()
        const newMessage = {
            email : this.form.email,
            name : this.form.name,
            message : this.form.message
        }
        this.$emit('new-message', newMessage)
        this.toastMessage()
        this.onReset(e)
      },
      toastMessage() {
        const h = this.$createElement

        const toastMessage = h(
            'p',
            { class: ['text-center', 'mb-0', 'toast-text'] },
            [
                //' Will respond as soon as possible '
                'Please read notice below contact form'
            ]
        )
        const toastTitle = h(
            'p',
            { class: ['mb-0', 'toast-title'] },
            [
                //' Successfully sent!! '
                'Not currently working'
            ]
        )
            this.$bvToast.toast([toastMessage], {
                title: [toastTitle],
                autoHideDelay: 5000,
                solid: true,
                variant: 'info'
            })
    },
    onReset(e) {
        e.preventDefault()
        this.form.email = ''
        this.form.name = ''
        this.form.message = ''
    }
    }
    
}
</script>

<style scoped>

    .body-control-form {
        background-color: #24305e;
        color: #f8e9a1;
        font-family: 'Abel', sans-serif;
    }

    .btn-outline {
        background-color: transparent;
        border: 2px solid #f8e9a1;
        height: 50px;
        width: 100px;
        color: #f8e9a1;
        width: 100%;
        transition: 1s;
    }

    .btn-outline:hover {
        background-color: #f8e9a1;
        color: #24305e;
        box-shadow: 0 0 20px #378785;
        -webkit-box-shadow: 0 0 20px #378785;
        transition: 1s;
    }

    .informations-card {
        background-color: #a8d0e6;
        color: #24305e;
        border-radius: 5px;
    }

    p, h1, .b-card-footer, .b-card-header, .b-card-body, li {
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    .toast-title {
        color: #24305e;
        font-family: 'Abel', sans-serif;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .toast-text {
        color: #24305e;
        font-family: 'Abel', sans-serif;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .contact-icons {
        color: #24305e;
    }
    
</style>