<template>
    <div class="body">
        <b-container>
            <b-row>
                <b-col md="4" offset-md="4" class="text-center mb-5">
                    <h1 class="label">Works</h1>
                </b-col>
            </b-row>
            <transition-group name="list" tag="work-slide">
                <work-slide v-for="carousel in carouselDivArr" :key="carousel.id" :showDivId="showDivId" :carousel="carousel"></work-slide>
            </transition-group>    
            <b-row class="mt-4">
                <b-col md="12" class="text-center">
                    <button class="div-prev mr-3" @click="divControlPrev">
                        <font-awesome-icon class="div-carousel-icon" icon="chevron-circle-left"/>
                    </button>
                    <button class="div-next ml-3" @click="divControlNext">
                        <font-awesome-icon class="div-carousel-icon" icon="chevron-circle-right"/>
                    </button>
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
import WorkSlide from './WorkSlide'
export default {
    name: 'Work',
    components: {
        WorkSlide
    },
    data() {
        return {
            carouselDivArr: [
                {
                    id: 0,
                    title: 'Get Car',
                    companyName: 'EVNJ Ltd.',
                    description: 'A company that offers affordable car rental service for you car rental needs.',
                    photo: '/img/server-down.b30469b7.jpg',
                    role: 'Junior Front-End Developer - Freelance',
                    btnClass: 'btn-outline-disabled mt-5',
                    btnText: 'Unfortunately the server is down',
                    disabledBtn: true,
                    technologies: [
                        '.Net MVC',
                        'Bootstrap',
                        'Razor HTML'
                    ]
                },
                {
                    id: 1,
                    title: 'Jabit',
                    companyName: '',
                    description: 'Jabit is an application for developers,it generates scaffold boiler-plate code from a given JSON schema.',
                    photo: '/img/server-maintenance.85679fdc.jpg',
                    role: 'Junior Front-End Developer - Open Source',
                    btnClass: 'btn-outline-disabled mt-5',
                    disabledBtn: true,
                    btnText: 'Server is under maintenance',
                    technologies: [
                        '.Net MVC',
                        'Bootstrap',
                        'Angular'
                    ]
                },
                {
                    id: 2,
                    title: 'Direct Wireless',
                    companyName: 'SCSI',
                    description: 'DirectWireless delivers high grade wireless alarm transmission to over 95% of the Australian population and is ideal for high risk sites, as well as a ready option for most new or existing alarm systems that rely on traditional PSTN phone lines for communications.',
                    photo: '/img/project-1.b1feceab.png',
                    role: 'Junior Full-Stack Developer - Contributor',
                    btnClass: 'btn-outline mt-5',
                    btnText: 'View',
                    disabledBtn: false,
                    technologies: [
                        '.Net Core',
                        'C#',
                        'Javascript',
                        'Jquery',
                        'Bootstrap',
                        'Razor HTML'
                    ]
                }
            ],
            showDivId : 0
        }
    },
    methods: {
        divControlPrev() {
            if(this.showDivId <= 0) {
                this.showDivId = 2
            } else {
                this.showDivId--
            }
            console.log(this.carouselDivArr)
        },
        divControlNext(){
            if(this.showDivId >= 2) {
                this.showDivId = 0
            } else {
                this.showDivId++
            }
            console.log(this.carouselDivArr)
        }
    }
}
</script>

<style scoped>
    .label {
        font-weight:700;
        color: #24305e;
        padding-bottom: 15px;
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    .body {
        background-color: #f8e9a1;
        padding-top: 140px;
        padding-bottom: 140px;
        color: #24305e;
        font-family: 'Abel', sans-serif;
    }

    .div-prev, .div-next {
        border: none;
        background-color: transparent;
        width: 50px;
    }

    .div-prev:focus, .div-next:focus {
        outline: none;
    }

    .div-carousel-icon {
        height: 50px;
        width: 50px;
        color: #24305e;
    }

    /* Transition */

    .list-enter-active {
        animation: listInAnimation 0.4s ease-in-out
    }

    .list-leave-active {
        animation: listOutAnimation 0.4s ease-in-out
    }

    @keyframes listInAnimation {
        from { transform: translateX(100%) }
        to { transform: translateX(0) }
    }

    @keyframes listOutAnimation {
        from { transform: translateX(0) }
        to { transform: translateX(-100%) }
    }
</style>