<template>
  <div>
    <div class="fb-comments-wrapper">
      <div class="fb-comments" data-href="https://sniperxzephyr.github.io/" data-colorscheme="dark" data-numposts="5" data-order-by="social" ></div>
    </div>
  </div>
</template>
<script>
export default {
  name:"facebookComment",
  data () {
    return {  
      startchat: false
    }
  },
  mounted(){
    this.init()    
  },
  methods: {
    init () {
      if (window.FB) {
          window.FB.init({
              appId      : '349778362793866',
              status     : true,
              xfbml      : true,
              version    : 'v8.0'
          })
      }
    }
  }
}
</script>
<style>

  .fb-comments {
    width: 100%;
  }

  .fb-comments-wrapper {
    color: #f8e9a1 !important;
    font-family: 'Abel', sans-serif !important;
  }
</style>