<template>
    <div class="body">
        <b-container class="text-center">
            <b-row>
                <b-col md="6" class="first-column">
                    <img class="contact-image mb-5" src="../assets/img/contact-icon.png" alt="contact-image">
                    <h2>Any Thoughts and Suggestions?</h2>
                    <p>Feel free to have a conversation with me.</p>

                    <div class="btn-group mt-4" role="group">
                        <b-button class="btn btn-outline" id="accordion" @click="toggleContactForm"><span class="btn-text" id="btn-text">Start a conversation</span></b-button>
                        <button class="btn btn-disabled"><font-awesome-icon class="btn-icon" icon="comment" /></button>
                    </div>
                </b-col>
                <b-col md="6" class="mt-3">
                    <hr id="target" hidden />
                    <ContactForm v-on:new-message=" newMessage" class="contact-form" id="contact-form"/>
                    <FacebookComment hidden/>
                </b-col>
            </b-row>
        </b-container>
    </div>    
</template>

<script>
import axios from 'axios'

import ContactForm from '../components/ContactForm'
import FacebookComment from '../components/FacebookComment'

export default {
    name: 'Contact',
    components: {
        ContactForm,
        FacebookComment
    },
    methods: {
        toggleContactForm() {
            var target = document.getElementById("target");
            var contactForm = target.nextElementSibling;

            if (contactForm.style.maxWidth) {
            contactForm.style.maxWidth = null;
            contactForm.style.maxHeight = null;
            contactForm.style.transition = "1s";
            } else {
            contactForm.style.maxWidth = "100%";
            contactForm.style.maxHeight = contactForm.scrollHeight + "px";
            contactForm.style.transition = "1s";
            }
        },
        newMessage(newMessage) {
            axios.post('https://localhost:44341/api/Comment', newMessage)
            .then(res => {
                this.comments = [...this.comments, res.data]
                /*this.toastMessageSuccess()*/
            })
            .catch(err => {
                /*this.toastMessageFailed()*/
                console.log(err);
            })
        },
        toastMessageSuccess() {
            const h = this.$createElement

            const toastMessage = h(
            'p',
            { class: ['text-center', 'mb-0', 'toast-text'] },
            [
                //' Will respond as soon as possible '
                'Please read notice below contact form'
            ]
            )
            const toastTitle = h(
            'p',
            { class: ['mb-0', 'toast-title'] },
            [
                //' Successfully sent!! '
                'Not currently working'
            ]
            )
            this.$bvToast.toast([toastMessage], {
                title: [toastTitle],
                autoHideDelay: 5000,
                solid: true,
                variant: 'info'
            })
        },
        toastMessageFailed() {
            const h = this.$createElement

            const toastMessage = h(
                'p',
                { class: ['text-center', 'mb-0', 'toast-text'] },
                [
                    ' Please try again later. '
                ]
            )
            const toastTitle = h(
                'p',
                { class: ['mb-0', 'toast-title'] },
                [
                    ' Failed to send'
                ]
            )
        this.$bvToast.toast([toastMessage], {
            title: [toastTitle],
            autoHideDelay: 5000,
            solid: true,
            variant: 'info'
            })
        },
    }
}
</script>

<style scoped>

    @media screen and (max-width: 767.5px) {
        .first-column {
            border-right: none !important;
            border-bottom: 10px solid #a8d0e6;
        }
    }

    .contact-form {
        max-width: 0;
        max-height: 0;
        overflow: hidden;
        transition: max-width 1s;
    }

    .body {
        background-color: #24305e;
        padding-top: 150px;
        padding-bottom: 150px;
        color: #f8e9a1;
        font-family: 'Abel', sans-serif;
    }

    p, h1, h2, .b-card-footer, .b-card-header, .b-card-body {
        -moz-user-select: none;
        -khtml-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
        color: #f8e9a1;
    }

    .first-column {
        border-right: 10px solid #a8d0e6;
        padding-top: 20px;
        padding-bottom: 20px;
    }

    .btn-outline {
        background-color: transparent;
        border: 1px solid #a8d0e6;
        color: #a8d0e6;
        font-size: 25px;
        border-radius: 25px;
        transition: 1s;
    }

    .btn-outline:hover {
        background-color: #a8d0e6;
        border: 1px solid #a8d0e6;
        color: #24305e;
        font-size: 25px;
        border-radius: 25px;
        box-shadow: 0 0 20px #378785;
        -webkit-box-shadow: 0 0 20px #378785;
        transition: 1s;
    }

    .btn-outline:focus,.btn-outline:active {
        box-shadow: none;
    }

    .btn-disabled {
        background-color: #a8d0e6;
        color: #24305e;
        font-size: 25px;
        border-radius: 25px;
    }

    .contact-image {
        width: 50%;
        border-radius: 50%;;
        box-shadow: 0 0 10px #378785;
    }

    .toast-title {
        color: #24305e;
        font-family: 'Abel', sans-serif;
        padding-top: 10px;
        padding-bottom: 10px;
    }

    .toast-text {
        color: #24305e;
        font-family: 'Abel', sans-serif;
        padding-top: 10px;
        padding-bottom: 10px;
    }

</style>